import {
  Component,
  Input,
  ViewChild,
  ElementRef,
  ViewEncapsulation,
  Directive,
} from '@angular/core';
import {VdlInkBar} from '../ink-bar';

/**
 * Navigation component matching the styles of the tab group header.
 * Provides anchored navigation with animated ink bar.
 */
@Component({
  moduleId: module.id,
  selector: '[vdl-tab-nav-bar]',
  templateUrl: 'tab-nav-bar.html',
  styleUrls: ['tab-nav-bar.css'],
  encapsulation: ViewEncapsulation.None,
})
export class VdlTabNavBar {
  @ViewChild(VdlInkBar) _inkBar: VdlInkBar;

  /** Animates the ink bar to the position of the active link element. */
  updateActiveLink(element: HTMLElement) {
    this._inkBar.alignToElement(element);
  }
}

/**
 * Link inside of a `vdl-tab-nav-bar`.
 */
@Directive({
  selector: '[vdl-tab-link]',
  host: {
    '[class.vdl-tab-link-active]': 'active === true'
  }
})
export class VdlTabLink {
  private _isActive: boolean = false;

  /** Whether the link is active. */
  @Input()
  get active(): boolean { return this._isActive; }
  set active(value: boolean) {
    this._isActive = value;
    if (value) {
      // todo(dhm): triggers updating the ink-bar which we should probably remove in favor of simple border bottom
      this._vdlTabNavBar.updateActiveLink(this._element.nativeElement);
    }
  }

  constructor(private _vdlTabNavBar: VdlTabNavBar, private _element: ElementRef) {}

}
